<template>
  <div>
    <div v-show="isLoading" class="collection-tip"><loading></loading></div>
    <div v-show="!isLoading">
      <other-list v-if="listData.length" :listData="listData"></other-list>
      <div v-else class="text-center collection-tip">暂无收藏</div>
    </div>
  </div>
</template>

<script>
  import dataUrl from '../api/DataUrl'
  import storage from '../api/Storage'
  import otherList from '../components/OtherList.vue'
  import loading from '../components/loading.vue'
  export default {
    data(){
      return {
        listData:[],
        isLoading:true
      }
    },
    mounted() {
      let vm = this;
      let user = storage.user();

      if (user && user.userName) {
        dataUrl.getCollectionTopics(user.userName).then((res)=>{
          vm.listData = res.body.data;
          this.isLoading = false;
        })
      }


    },
    components:{
      otherList,loading
    }
  }
</script>

<style>
  .collection-tip {
    margin-top: 20px;
  }
</style>



